export type Pokemon = {
  link: string;
  pid: number;
  paldea: number | null;
  kitakami: number | null;
  hisui: number | null;
  nameZh: string;
  nameEn: string;
  nameJp: string;
  altForm: string | null;
  hiddenAbility: string;
  types: string[];
  abilities: string[];
  baseStats: number[];
  baseStatsTotal: number;
  EVs: number[];
  source: string;
  genderRatio: number;
};

type Move = {
  pid: number;
  nameZh: string;
  nameJp: string;
  nameEn: string;
  type: string;
  category: string;
  power: number;
  accuracy: number;
  PP: number;
  description: string;
};

type LevelingUp = Move & { level: number };

type EggMove = Move & { level: number };

type TM = Move & {
  TMPid: number;
  leaguePoint: number;
  materials: {
    material: string;
    count: number;
    pm: string;
  }[];
};

type MoveInfo = {
  levelingUps: LevelingUp[];
  eggMoves: EggMove[];
  TMs: TM[];
};

type Evolve = {
  from: string;
  to: string;
  fromNameZh: string;
  toNameZh: string;
  condition: string;
};

export type FullPokemon = Pokemon & {
  moves: MoveInfo;
  evolves: (Evolve & { evolves?: Evolve[] })[];
};

export type PokedexFrom = 'kitakami' | 'paldea' | 'hisui';

export const PokedexList = ['kitakami', 'paldea', 'hisui'];

